import 'uni-pages';
// @ts-ignore
wx.__webpack_require_UNI_MP_PLUGIN__ = __webpack_require__;import App from './App'
/*引入App.Vue(全局js文件和全局样式文件)*/


import Vue from 'vue'
Vue.config.productionTip = false //关闭生产环境提醒
App.mpType = 'app'

// 引入仓库
import store from './store/index.js'

try {
  function isPromise(obj) {
    return (
      !!obj &&
      (typeof obj === "object" || typeof obj === "function") &&
      typeof obj.then === "function"
    );
  }

  // 统一 vue2 API Promise 化返回格式与 vue3 保持一致
  uni.addInterceptor({
    returnValue(res) {
      if (!isPromise(res)) {
        return res;
      }
      return new Promise((resolve, reject) => {
        res.then((res) => {
          if (res[0]) {
            reject(res[0]);
          } else {
            resolve(res[1]);
          }
        });
      });
    },
  });
} catch (error) { }

import Recommend from '@/components/Recommend/Recommend'
Vue.component('Recommend',Recommend)
import cateList from '@/components/cateList/cateList'
Vue.component('cateList',cateList)

// 挂载仓库
const app = new Vue({
  ...App,
  store
})
app.$mount()